:root {
  --main-text-color: #000000;
  --secondary-text-color: #5e5c5c;
  --header-text-color: #ffffff;

  --content-color: #ffffff;
  --header-color: #311b92;
  --body-color: #ede7f6;
}

* {
  margin: 0;
  outline: none;

  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}

body {
  background-color: var(--body-color);
  letter-spacing: 1px;
}

.header {
  background-color: var(--header-color);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;

  margin: auto 0;
  width: 100%;
}

.header__content {
  max-width: 1360px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 1rem;
  margin: 0 auto;
}

.header__content__logo {
  color: var(--header-text-color);
  display: block;
  margin: auto;
}

.header__content__login_button {
  color: var(--header-text-color);
  border: none;
  background: none;
  cursor: pointer;
  text-align: center;
}

.main_content {
  max-width: 1360px;
  margin: 0 auto;
  padding: 20px 20px;
}

.post_preview {
  background-color: var(--content-color);
  -webkit-box-shadow: 0 4px 8px 0 regba(0, 0, 0, 0, 0.2);
          box-shadow: 0 4px 8px 0 regba(0, 0, 0, 0, 0.2);
  width: auto;
}

.post_preview__header {
}

.post_preview__header__username {
  font-size: 1rem;
  padding: 1rem 1rem 0;
  color: var(--main-text-color);
}

.post_preview__header__publication_date {
  font-size: 0.7rem;
  color: var(--secondary-text-color);
  padding: 0.2rem 1rem 0;
}

.post_preview__header__title {
  font-size: 2rem;
  padding: 0.2rem 1rem 0;
}

.post_preview__header__tags {
  font-size: 0.7rem;
  color: var(--secondary-text-color);
  padding: 0 1rem 0.5rem;
}

.post_preview__content {
  padding: 1rem;
}

.post_preview__footer {
  padding: 1rem;
  text-align: right;
}

.post_preview__footer__detail {
  text-decoration: none;
}

.post_view {
  background-color: var(--content-color);
  -webkit-box-shadow: 0 4px 8px 0 regba(0, 0, 0, 0, 0.2);
          box-shadow: 0 4px 8px 0 regba(0, 0, 0, 0, 0.2);
  width: auto;
}

.post_view__header {
  padding: 1rem;
}

.post_view__content {
  padding: 0 1rem;
}

.post_view__footer {
  padding: 1rem;
}

.post_view__footer__username {
  font-size: 1rem;
}

.post_view__footer__publication_date {
  font-size: 0.7rem;
  color: var(--secondary-text-color);
}

.auth_form {
  position: absolute;
  padding: 2rem;
  background-color: var(--content-color);
  left: calc((100vw - 500px) / 2);
  right: calc((100vw - 500px) / 2);
}

.auth_form_overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--header-color);
  opacity: 50%;
}

.auth_form__row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 1rem;
}

.auth_form__footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.auth_form__footer__link_button {
  border: none;
  cursor: pointer;
  background: none;
  color: var(--header-color);
}

.auth_form__footer__main_button {
  cursor: pointer;
}

.user_view {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-right: 3rem;
}

.user_view:hover .user_view__menu_item {
  display: block;
  cursor: pointer;
}

.user_view__username {
  color: var(--header-text-color);
  cursor: pointer;
}

.user_view__menu_item {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
          box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
}

.comments {
  margin: 0.5rem;
}

.comments__comment {
  background-color: var(--content-color);
  padding: 1rem;
}

.commetns__comment__footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

@media only screen and (max-width: 480px) {
  .auth_form {
    left: 0;
    right: 0;
  }
}

@media only screen and (max-width: 768px) {
  .auth_form {
    left: 0;
    right: 0;
  }
}

